.page.selectedPage {
    font-weight: bold;
    color: white;
    border-radius: 5px;
    border: 2px solid white;

}

.page {
    cursor: pointer;
    margin: 3px;
    padding: 5px;
    color: grey;
}

.nextPrevButton {
    margin: 5px;
    border: 2px double white;
    border-radius: 30px; 
    height: 25px;
    background-color: grey;
    color: white;
    cursor: pointer;
}

.arrows {
    height: 15px;
    width: 15px;
}

.buttonArrow {
    background-color: transparent;
}